.application-card {
  border: 1px solid #ccc;
  border-radius: 10px;
  padding: 16px;
  margin-bottom: 16px;
  background-color: #fefefe;
  box-shadow: 0 1px 4px rgba(0,0,0,0.1);
}

.application-card h3 {
  margin-top: 0;
  color: #2b2d42;
}

.application-card p {
  margin: 6px 0;
  font-size: 15px;
}

.status {
  text-transform: capitalize;
  font-weight: bold;
  padding: 2px 8px;
  border-radius: 6px;
}

.status.pending {
  color: #f9a825;
}

.status.accepted {
  color: green;
}

.status.rejected {
  color: red;
}

.status.interview {
  color: #2196f3;
}

.withdraw-btn {
  margin-top: 12px;
  padding: 8px 12px;
  background-color: #ef233c;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.withdraw-btn:hover {
  background-color: #d90429;
}
