/*General Layout*/
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
  color: #222;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  cursor: pointer;
  font-weight: 500;
  border: none;
  border-radius: 5px;
  padding: 10px 16px;
  background-color: #007bff;
  color: #fff;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #0056b3;
}

input,
select,
textarea {
  font-family: inherit;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  box-sizing: border-box;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/*Navbar*/
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  padding: 15px 30px;
  border-bottom: 1px solid #ddd;
}

.navbar a {
  margin: 0 10px;
  font-weight: 500;
}

/*Dashboard Layouts*/
.dashboard {
  padding: 40px 20px;
}

.dashboard h1 {
  font-size: 28px;
  margin-bottom: 20px;
}

.card {
  background-color: #fff;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
}

/*Job Card*/
.job-card {
  display: flex;
  flex-direction: column;
  border: 1px solid #e1e1e1;
  border-radius: 6px;
  padding: 16px;
  background-color: #fff;
  margin-bottom: 20px;
  transition: box-shadow 0.2s ease;
}

.job-card:hover {
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
}

.job-card h3 {
  margin-top: 0;
  font-size: 20px;
}

.job-card .meta {
  font-size: 14px;
  color: #666;
  margin-bottom: 8px;
}

/*Application Card*/
.application-card {
  border: 1px solid #e1e1e1;
  padding: 16px;
  border-radius: 6px;
  background-color: #fff;
  margin-bottom: 16px;
}

.application-card .status {
  font-weight: bold;
  padding: 4px 10px;
  border-radius: 4px;
  display: inline-block;
  margin-top: 10px;
}

.status.pending {
  background-color: #fff3cd;
  color: #856404;
}

.status.accepted {
  background-color: #d4edda;
  color: #155724;
}

.status.rejected {
  background-color: #f8d7da;
  color: #721c24;
}

/*Toastify Overrides*/
.Toastify__toast {
  font-size: 14px;
  border-radius: 6px;
}

.Toastify__toast--success {
  background-color: #d4edda;
  color: #155724;
}

.Toastify__toast--error {
  background-color: #f8d7da;
  color: #721c24;
}
.logout-button {
  background-color: #e74c3c;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s;
}

.logout-button:hover {
  background-color: #c0392b;
}
