.notfound-container {
  text-align: center;
  margin-top: 80px;
  padding: 20px;
}

.notfound-container h1 {
  font-size: 72px;
  margin-bottom: 20px;
  color: #c0392b;
}

.notfound-container p {
  font-size: 18px;
  margin-bottom: 20px;
}

.notfound-container a {
  background-color: #3498db;
  color: white;
  padding: 10px 16px;
  text-decoration: none;
  border-radius: 6px;
  font-weight: 500;
}

.notfound-container a:hover {
  background-color: #2980b9;
}
